<style>
table.dataTable thead .sorting:after,
table.dataTable thead .sorting:before,
table.dataTable thead .sorting_asc:after,
table.dataTable thead .sorting_asc:before,
table.dataTable thead .sorting_asc_disabled:after,
table.dataTable thead .sorting_asc_disabled:before,
table.dataTable thead .sorting_desc:after,
table.dataTable thead .sorting_desc:before,
table.dataTable thead .sorting_desc_disabled:after,
table.dataTable thead .sorting_desc_disabled:before {
  bottom: .5em;
}

</style>
<template>
    <div id="enrolledstudent">
        <div class="container">
            <h2>Enrolled Student</h2>
<table id="stdenrolled" class="table table-striped table-bordered" style="width:100%">
        <thead>
            <tr>
                <th>Reference ID</th>
                <th>Student Level</th>
                <th>Student Classification</th>
                <th>Student Status</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="enrolled in enrollies" :key="enrolled.id">
                <td>{{enrolled.ref_id}}</td>
                <td>{{enrolled.std_level}}</td>
                <td>{{enrolled.std_class}}</td>
                <td>{{enrolled.std_stat}}</td>

            </tr>
        </tbody>
        <tfoot>
            <tr>
                <th>Reference ID</th>
                <th>Student Level</th>
                <th>Student Classification</th>
                <th>Student Status</th>
            </tr>
        </tfoot>
    </table>
        </div>

    </div>
</template>

<script>
import firebase from 'firebase'
import db from './firebaseInit'
export default {
    name: 'enrolledstudent',
    data(){
        return {
            enrollies: [],
            tempID: ''
        }
    },
    mounted(){
        /*
    $(document).ready(function() {
        $('#stdenrolled').DataTable({
            "data" : [ this.enrollies],
            "columns" : [
                {"data": "ref_id"},
                {"data": "std_level"},
                {"data": "std_class"},
                {"data": "std_stat"}
            ]

        });
      } );
        */

    },
    created (){
       db.collection('studentinfo').get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'ref_id': doc.data().ref_id,
                    'std_level': doc.data().std_level,
                    'std_class': doc.data().std_class,
                    'std_stat': doc.data().std_stat
                }
                //console.log(data)
                this.enrollies.push(data)
            })
        })  
        
        var refID = firebase.database().ref().child('studentinfo')
        console.log(refID)
    }
}
</script>


