<template>
    <div id="studentinfo">
        <h3 class="center bg-primary text-center text-white">Student information</h3>
        <div v-if="tempId === null" class="container">
            <h4>Reference ID is not registered</h4>
            <i>To enroll student please click <router-link to="/enrollment">here</router-link></i>
        </div>
        <div v-else class="container">
            <form @submit.prevent="savePersonalInfo">
                <div class="row">
                    <div class="col-md-6">
                         <div class="card" v-if="std_img === ''">
                            <div class="card-body">
                                <h5 class="card-title">Upload Student Picture</h5>
                                <input type="file" @change="onFileSP($event)">
                            </div>
                        </div>
                        <div class="card" v-else>
                            <div class="card-body">
                                <img :src="std_img" style="width:100%">
                                <h5 class="card-title">Student Picture</h5>
                                <button class="btn btn-secondary" @click="removeSP()">Remove</button>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-6">
                        <div class="row">
                            <div class="col-md-12">
                                <label>Last Name</label>
                                <input type="text" v-model="lastname" class="form-control" required>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-12">
                                <label>Given Name</label>
                                <input type="text" v-model="firstname" class="form-control" required>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-12">
                                <label>Middle Name</label>
                                <input type="text" v-model="middlename" class="form-control" required>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="row">
                    <div class="col-md-12">
                        <label>Address</label>
                        <input type="text" v-model="std_add" class="form-control" required>
                    </div>
                </div>
                <div class="row">
                    <div class="col-sm-4">
                        <label>Birthday</label>
                        <input type="date" v-model="std_bday" class="form-control" required>
                    </div>
                    <div class="col-sm-4">
                        <label>Age</label>
                        <input type="number" v-model="std_age" class="form-control" required>
                    </div>
                    <div class="col-sm-4">
                        <label>Gender</label>
                        <select v-model="std_gender" class="form-control" required>
                        <option value="male">Male</option>
                        <option value="female">Female</option>
                        </select>
                    </div>
                </div>
                <div class="row">
                    <div class="col-sm-6">
                        <label>Student e-Mail Address</label>
                        <input type="email" v-model="std_email" class="form-control">
                    </div>
                    <div class="col-sm-6">
                        <label>Student Contact Number</label>
                        <input type="number" v-model="std_contact" class="form-control">
                    </div>
                </div>
                <hr>
                <div class="row">
                    <div class="col-sm-6">
                        <label>Mother's Name</label>
                        <input type="text" v-model="std_mother" class="form-control" required>
                    </div>
                    <div class="col-sm-6">
                        <label>Father's Name</label>
                        <input type="text" v-model="std_father" class="form-control" required>
                    </div>
                </div>
                <hr>
                <div class="row">
                    <div class="col-sm-6">
                        <label>Guardian's Name (if Parents are not around)</label>
                        <input type="text" v-model="std_guard" class="form-control" required>
                    </div>
                    <div class="col-sm-6">
                        <label>Guardian/Parent's Contact Number</label>
                        <input type="number" v-model="parent_contact" class="form-control">
                    </div>
                </div>
                <hr>
                <div class="row">
                    <div class="col">
                        <router-link to="/" class="btn btn-danger">CANCEL</router-link>
                    </div>
                    <div class="col text-right">
                        <button type="submit" class="btn btn-primary" >SUBMIT</button>
                    </div>
                </div>
            </form>
        </div>
        
    </div>
</template>

<script>
import db from './firebaseInit'
import firebase from 'firebase'
export default {
    name: 'studentinfo',
    data(){
        return {
            ref_id: this.$route.params.ref_id,
            std_img: '',
            lastname: null,
            firstname: null,
            middlename: null,
            std_add: null,
            std_bday: null,
            std_age: null,
            std_gender: null,
            std_email: null,
            std_contact: null,
            std_mother: null,
            std_father:null,
            std_guard: null,
            parent_contact:null,
            datereg: null,
            timestamp: null,

            tempId: null
        }
    },
    mounted () {
       db.collection('studentinfo')
       .where('ref_id','==',this.ref_id)
       .get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                this.tempId = doc.id
            })
        })

         this.datereg = new Date().getMonth()+'-'+new Date().getDate()+'-'+new Date().getFullYear()
         this.timestamp = new Date().getHours()+':'+new Date().getMinutes()+':'+new Date().getUTCSeconds()
    },
    methods:   {
        savePersonalInfo (){
            /*
            db.collection('personalInfo').add({
                ref_id: this.ref_id,
                std_img: this.std_img,
                lastname: this.lastname,
                firstname: this.firstname,
                middlename: this.middlename,
                std_add: this.std_add,
                std_bday: this.std_bday,
                std_age: this.std_age,
                std_gender: this.std_gender,
                std_email: this.std_email,
                std_contact: this.std_contact,
                std_mother: this.std_mother,
                std_father:this.std_father,
                std_guard: this.std_guard,
                parent_contact: this.parent_contact,
                date_reg: this.datereg,
                timestamp: this.timestamp
            })
            .then(docRef => this.$router.push('/thankyou/'+this.ref_id))
            .catch(error => console.log(error))
            */
           db.collection('studentinfo').doc(this.tempId).update({
                 std_img: this.std_img,
                lastname: this.lastname,
                firstname: this.firstname,
                middlename: this.middlename,
                std_add: this.std_add,
                std_bday: this.std_bday,
                std_age: this.std_age,
                std_gender: this.std_gender,
                std_email: this.std_email,
                std_contact: this.std_contact,
                std_mother: this.std_mother,
                std_father:this.std_father,
                std_guard: this.std_guard,
                parent_contact: this.parent_contact,
                date_reg: this.datereg,
                timestamp: this.timestamp              
           })
           .then(this.$router.push('/thankyou/'+this.ref_id))
        },
         onFileSP(e) {
            var input = e.target
            // Ensure that you have a file before attempting to read it
            if (input.files && input.files[0]) {
                // create a new FileReader to read this image and convert to base64 format
                var reader = new FileReader();
                // Define a callback function to run, when FileReader finishes its job
                reader.onload = (e) => {
                    // Note: arrow function used here, so that "this.imageData" refers to the imageData of Vue component
                    // Read image as base64 and set to imageData
                    this.std_img = e.target.result;
                    //console.log(e.target.result)
                }
                // Start the reader job - read file as a data url (base64 format)
                reader.readAsDataURL(input.files[0]);

            }
           },
        removeSP: function () {
            this.std_img = ''; 
            },
    }
}
</script>


